.greeting_title{
    color: #000000;
    font-size: 72px;
}

.greeting_mainText {
    font-size: 32px;
}

.greeting_wrapper{
    flex: 1;
    padding: 80px 160px 80px 160px;
}

.greeting_main {
    display: flex;
    justify-content: space-between;
}

@media ( min-width: 0px) {
    .greeting_main {
        flex-direction: column;
    }
    .greeting_wrapper {
        padding: 40px 20px 20px 20px;
    }
    .greeting_title {
        font-size: 32px;
        text-align: center;
        margin-bottom: 40px;
    }
    .greeting_mainText {
            font-size: 24px;
            text-align: center;
    }

}

@media (min-width: 768px) {
   
.greeting_main {
        flex-direction: row;
    }
    .greeting_wrapper {
        padding: 40px 80px 40px 80px;
    }
        .greeting_title {
            font-size: 36px;
        }

                .greeting_mainText {
                    font-size: 20px;
                    text-align: left;
                }
}
